.fullpage {
    width: 100vw;
    height: 3000px;
}

.ContentAd_container {
    width: 100vw;
    height: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: orangered; */
}

.ContentAd {
    width:  calc(100vw - 170px);
    margin: 80px 0px;
    height: calc(100vh - 160px);
    /* border: black 2px solid; */
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(../img/TestImg.png);  
    background-size: cover;
    background-size: cover;
    background-position: bottom;
    background-color: orange;
    background-blend-mode: multiply;
}

.textinfo {
    /* background-color: orange; */
    color: white;
    width: 90%;
    height: 75%;
    display: flex;
    flex-flow: column nowrap;
}

.textbox {
    width: 100%;
    height: fit-content;
    margin-top: auto;
    margin-bottom: 0;
}
.title {
    font-size: 30px;
    font-family: "GMarket Bold";
    font-weight: lighter;
    margin-bottom: 10px;
}

.summary {
    margin-bottom: 5px;
}

.testingImg {
    width: 100%;
    height: auto;
    margin-bottom: 285px;
    z-index: -1;
}



.movie_container {
    width: 100vw;
    height: fit-content;
    display: flex;
    justify-content: center;
}

.movie_wrap {
    display: grid;
    grid-column-gap: 22px;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: auto;
    /* flex-flow: row wrap; */
    width: calc(100vw - 166px);
    height: fit-content;
    /* background-color: orange; */
    justify-content: space-between;
}